.navbar {
  width: 49rem;
  color: white;
}

#navID {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding-right: 2rem;
}

.brand-title {
  margin: 1.5rem;
}

.navbar-links ul {
  margin: 0;
  padding: 0;
  display: flex;
}
.navbar-links li {
  list-style: none;
  font-size: 1.2rem;
}
.navbar-links a {
  text-decoration: none;
  color: white;
  padding: 1rem 1.25rem;
  display: block;
}
.navbar-links a:hover {
  color: #bde59c;
}
.navbar-logo {
  width: 9rem;
  height: auto;
  padding-left: 1rem;
  padding-right: 1rem;
}
.toggle-button {
  position: absolute;
  top: 1.3rem;
  left: 1rem;
  margin-right: 1rem;
  display: none;
  flex-direction: column;
  justify-content: space-between;
  width: 1.5rem;
  height: 1rem;
}

.toggle-button .bar {
  height: 0.15rem;
  width: 100%;
  background-color: white;
  border-radius: 10%;
}

.mobile-logo {
  display: none;
}

/* Navbar collapse at specific width */
@media (max-width: 1054px) {
  .toggle-button {
    display: flex;
  }
  .navbar-links {
    display: none;
    width: 100%;
  }
  .active {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .navbar {
    flex-direction: column;
    align-items: flex-start;
  }
  .navbar-links ul {
    width: 100%;
    flex-direction: column;
  }
  .navbar-links li {
    list-style: none;
    font-size: 1rem;
  }
  #navID {
    justify-content: flex-start;
  }
  .mobile-logo {
    display: block;
    height: 2rem;
    padding-right: 1rem;
    padding-left: 1rem;
    top: 1.4rem;
  }
  .mobile-header {
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: relative;
    left: 2rem;
  }
  .navbar-logo {
    padding-right: 1rem;
  }
}

@media (max-width: 450px) {
  .mobile-logo {
    display: block;
    height: 2rem;
    padding-right: 0.25rem;
    padding-left: 0.25rem;
    top: 1.4rem;
  }
  .navbar-logo {
    width: 9rem;
    height: auto;
    padding-left: 1rem;
    padding-right: 1rem;
    margin-left: 0rem;
    margin-right: 0rem;
  }
  .navbar-logo {
    width: 8rem;
    height: auto;
    padding-left: 1rem;
    padding-right: 1rem;
  }
}
